  src: local("Whitney"), url("../../fonts/Whitney-Semibold Regular.woff2") format("woff2"), url("../../fonts/Whitney-Semibold Regular.woff") format("woff"); }

@font-face {
  font-family: "Segoe UI";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("SegoeUI"), url("../../fonts/SegoeUI.woff2") format("woff2"), url("../../fonts/SegoeUI.woff") format("woff"); }

@font-face {
  font-family: "Segoe UI";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("SegoeUI"), url("../../fonts/SegoeUI-SemiBold.woff2") format("woff2"), url("../../fonts/SegoeUI-SemiBold.woff") format("woff"); }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  scroll-behavior: smooth; }

body {
  max-width: 1185px;
  height: 100%;
  margin: 0 auto; }

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

a {
  text-decoration: none; }

ul,
ol {
  padding: 0;
  margin: 0;
  list-style: none; }

h1,
h2,
h3,
h4,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  padding: 0; }

img {
  display: block;
  max-width: 100%;
  padding: 0;
  margin: 0; }

input,
button,
textarea,
select {
  font: inherit; }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%); }

.disable-scroll {
  height: 100%;
  overflow: hidden; }

a {
  text-decoration: none; }

.main-header {
  display: grid;
  grid-template-columns: 242px 1fr;
  justify-items: end;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 24px 0 24px; }

.main-nav__wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-right: 23px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between; }

.main-nav__list {
  margin-right: 135px; }

.intro {
  padding: 250px 0 180px 0; }

.intro__title-block {
  max-width: 527px; }
  .intro__title-block p {
    margin-top: 30px; }

.intro__link {
  display: inline-block;
  vertical-align: middle;
  margin-top: 60px; }

.benefits {
  padding: 130px 0; }

.benefits__wrap {
  margin-top: 50px; }

.benefits__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px; }

.benefits__item {
  padding: 21px 10px 21px 40px; }

.benefits__item p {
  margin-top: 22px; }

.register {
  padding-bottom: 185px; }

.register__link {
  display: inline-block;
  vertical-align: middle;
  margin-top: 46px; }

.modal {
  display: none;
  max-width: 490px;
  padding: 60px 60px 55px 59px;
  margin: 0 auto; }
